import React, { useState } from 'react';
import '../Styles/Stages.css'; // Assurez-vous d'avoir un fichier CSS pour le style

const stageListings = [
  // Exemple d'offres de stages
  { id: 1, title: "Stage Dveloppeur Frontend", description: "Contribuez  des projets web." },
  { id: 2, title: "Stage Dveloppeur Backend", description: "Participez au dveloppement des API." },
  { id: 3, title: "Stage Designer UI/UX", description: "Aidez  concevoir des interfaces utilisateur." },
  { id: 4, title: "Stage Chef de Projet Junior", description: "Assistez  la gestion de projets." },
  { id: 5, title: "Stage Analyste de Donnes", description: "Effectuez des analyses de donnes." },
  { id: 6, title: "Stage Dveloppeur Fullstack", description: "Participez au dveloppement de solutions compltes." },
  { id: 7, title: "Stage Ingnieur DevOps", description: "Apprenez  optimiser les dploiements." },
  // Ajoutez d'autres stages si ncessaire
];

function Stages() {
  const [currentPage, setCurrentPage] = useState(1);
  const stagesPerPage = 6;

  // Calcul des indices pour la pagination
  const indexOfLastStage = currentPage * stagesPerPage;
  const indexOfFirstStage = indexOfLastStage - stagesPerPage;
  const currentStages = stageListings.slice(indexOfFirstStage, indexOfLastStage);

  const totalPages = Math.ceil(stageListings.length / stagesPerPage);

  return (
    <div className="stages-container">
      <h1>Offres de Stages</h1>
      <div className="stage-listings">
        {currentStages.map(stage => (
          <div key={stage.id} className="stage-card">
            <h2>{stage.title}</h2>
            <p>{stage.description}</p>
          </div>
        ))}
      </div>
      <div className="pagination">
        <button onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))} disabled={currentPage === 1}>
          Prcdent
        </button>
        <span>Page {currentPage} sur {totalPages}</span>
        <button onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))} disabled={currentPage === totalPages}>
          Suivant
        </button>
      </div>
      <div className="ads-section">
        {/* Intgration des annonces Google Ads */}
        <iframe
          src="https://www.profitableratecpm.com/b9gz1b6sx?key=e1c5ad83639643368d0bda4e813f4875"
          width="100%"
          height="250"
          title="Google Ads"
          style={{ border: 'none' }}
        ></iframe>
      </div>
    </div>
  );
}

export default Stages;